body {
  margin: 0;
}

html {
  font-family: Roboto, serif;
  font-weight: 400;
}

.logo {
  align-items: center;
  display: flex;
}

.logo-img {
  background-image: url("logo.eddae0ff.png");
  background-position: center;
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
}

.header {
  justify-content: center;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header__row {
  box-sizing: border-box;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.nav {
  margin: 0;
  padding: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin-left: 20px;
  list-style: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s linear;
  display: block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.catalog {
  --column-count: 1;
  box-sizing: border-box;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, serif;
  font-weight: 400;
  transition: all .3s linear;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  margin-inline: 3px;
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: contain;
  background-origin: border-box;
  height: 134px;
  margin-bottom: 40px;
  padding: 0;
  display: flex;
}

.card__text {
  margin: 0;
  padding: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s linear;
}

.card__text--product-code {
  color: #616070;
  margin-bottom: 16px;
}

.card__text--reviews {
  color: #060b35;
  align-self: center;
}

.card__row {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__row--feedback {
  margin-bottom: 24px;
}

.card__row--price {
  margin-bottom: 16px;
}

.card__text--price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__text--costs {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s linear;
  display: flex;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
